i18nstr=i18nstr.split("-")[0];
}
var  qurl=_this.requestUrl+"sciencepopularization/sciencepopularizations"
var param={"keyword":"","pageno":"1","pagesize":"10","qtype":"0","language":i18nstr}
_this.$commonAjax("POST",qurl,param,function(data){

_this.dataObj.d_data=true;
_this.verificationShowLoad();

if(data.data!=""){
var datalist=JSON.parse(data.data);
datalist=datalist;
var basList=new Array();
for(var i=0;i<datalist.length;i++){

var obj=new Object();
obj.id=datalist[i].id;
obj.url="javascript";
obj.img=datalist[i].swiperurl;
var localestr= _this.$i18n.locale
obj.title=datalist[i].title;
basList.push(obj);
}
_this.propaganda_list=basList;
}

},function(){
_this.showBottomToast(_this.$t('requestFailed'))
var appData = require("../../assets/json/propagandaListchange.json");
