,getWeatherData(){
var _this=this;
console.log("get curr weather!!")
var qurl=_this.requestUrl+"phone/aomenweatherforecast"

var weatherUrl="http://www.smg.gov.mo/smg/latestWeather/c_7daysforecast.xml";
var currweathUrl="http://xml.smg.gov.mo/c_actual_brief.xml";
_this.$commonAjax("GET",qurl,{"url":currweathUrl+","+weatherUrl},function(rdata){
_this.$vux.loading.hide();
console.log(rdata);
for(var key in rdata){
console.log(key)
if(key==currweathUrl){
console.log(rdata[key])
_this.currObj=JSON.parse(rdata[key]);
_this.dataObj.w_curr=true;
}
if(key==weatherUrl){
console.log("key==>"+weatherUrl)
var data=JSON.parse(rdata[key]);
var date=new Date();
var fomatdate=date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
var tomorrwDate = new Date(date.getTime() + 24*60*60*1000);
var tomorrw=tomorrwDate.getFullYear()+"-"+(tomorrwDate.getMonth()+1)+"-"+tomorrwDate.getDate();
var dataList=data.Custom.WeatherForecast;
_this.dataObj.w_day=true;
_this.twoWeather.today.state=dataList[0].WeatherStatus;
_this.twoWeather.today.Temperature=dataList[0].Temperature[1].Value+"/"+dataList[0].Temperature[0].Value;
_this.twoWeather.tomorrow.state=dataList[1].WeatherStatus;
_this.twoWeather.tomorrow.Temperature=dataList[1].Temperature[1].Value+"/"+dataList[1].Temperature[0].Value;
}
console.log(data[key]);
}



_this.verificationShowLoad();
},function(e){
_this.dataObj.w_curr=true;
_this.verificationShowLoad();
},true)
